html {
  height: 100%;
  box-sizing: border-box; }

footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  background-color: #efefef;
  text-align: center; }

body {
  position: relative;
  margin: 0;
  padding-bottom: 6rem;
  min-height: 100%; }
  body .content {
    margin: 0 auto;
    padding-top: 64px;
    max-width: 640px;
    width: 94%; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.center {
  text-align: center; }

li {
  color: #2E4053; }

th {
  font-family: 'Inconsolata', monospace;
  color: #2E4053;
  font-size: 1.5em; }

td {
  font-family: 'Cantarell', sans-serif;
  color: #85929E;
  font-size: 1.3em; }

a {
  color: #01A9DB; }
  a:hover {
    text-decoration: none;
    color: #04B4AE; }

.quote blockquote {
  border-left: none;
  overflow: hidden;
  padding-top: -100px;
  margin-top: -10px;
  width: inherit;
  border-radius: 3px;
  box-shadow: 2px 2px 2px 2px #ECF0F1; }

.quote h2 {
  text-align: left;
  margin-top: -10px;
  margin-bottom: -15px;
  font-weight: bold;
  font-size: 100px;
  font-family: 'Lora', serif;
  color: #85929E; }

.quote h3 {
  font-family: 'Lora', serif;
  color: #85929E;
  text-align: center;
  font-size: 2em;
  margin-top: -50px; }

.row {
  width: 90vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -45vw;
  margin-right: -45vw; }
  .row h2 {
    text-align: center; }
  .row a {
    color: #85929E;
    text-decoration: none; }
    .row a :hover {
      color: #F7F9F9;
      background-color: #85929E; }



